import React from 'react';
import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import Subheader from 'material-ui/Subheader';
import StarBorder from 'material-ui/svg-icons/toggle/star-border';

var injectTapEventPlugin = require("react-tap-event-plugin");
injectTapEventPlugin();

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';

const items = [];
for (let i = 1; i < 11; i++ ) {
  items.push(<MenuItem value={i} key={i} primaryText={`${i}`} />);
}

/**
 * With the `maxHeight` property set, the Select Field will be scrollable
 * if the number of items causes the height to exceed this limit.
 */
class DropDownMenuLongMenuExample extends React.Component {
  state = {
    value: 1,
  };

  handleChange = (event, index, value) => {
    this.setState({value});
  };

  render() {
    return (
      <SelectField
        value={this.state.value}
        onChange={this.handleChange}
        fullWidth="true"
        // style={{backgroundColor: "white"}}
      >
        {items}
      </SelectField>
    );
  }
}

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    width: 500,
    overflowY: 'auto',
  },
};

const tilesData = [
  {
    img: 'http://shop.farmboxdelivery.com/files/Artichokes%20duo.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/charleeyb/Sweet%20bell%20pepper.1.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/all_images/untitled%20folder/broccoli_01.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/all_images/brussel.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/lettuce%20romain_01.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/all_images/turmeric.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'http://shop.farmboxdelivery.com/files/all_images/untitled%20folder/Juicing%20Apples.jpg',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
  {
    img: 'https://cdn.shopify.com/s/files/1/1199/6336/products/farmstand_avocado_hass_shopify_20160509_large.jpg?v=1462930779',
    name: 'Brocolli',
    amount: '1kg',
    price: '25',
  },
];

/**
 * A simple example of a scrollable `GridList` containing a [Subheader](/#/components/subheader).
 */
const GridListExampleSimple = () => (
  <div style={styles.root}>
    <GridList
      cols={1}
      style={styles.gridList}
    >
      <Subheader>Vegetables</Subheader>
      {tilesData.map((tile) => (
        <GridTile
          key={tile.img}
          title={tile.name}
          subtitle={<span>{tile.amount}       -         <b>{tile.price}/-</b></span>}
          titleBackground="linear-gradient(to top, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0.3) 70%,rgba(0,0,0,0) 100%)"
          actionIcon={<DropDownMenuLongMenuExample />}
        >

          <img src={tile.img}/>
        </GridTile>
      ))}
    </GridList>
  </div>
);

export default GridListExampleSimple;



