import React from 'react';
import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import StarBorder from 'material-ui/svg-icons/toggle/star-border';
import Subheader from 'material-ui/Subheader';
import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';

import DropDownMenuLongMenuExample from './DropDownMenuLongMenuExample'

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    display: 'flex',
    flexWrap: 'nowrap',
    overflowX: 'auto',
  },
  titleStyle: {
    color: 'rgb(0, 188, 212)',
  },
};

/**
 * This example demonstrates the horizontal scrollable single-line grid list of images.
 */
class GridListExampleSingleLine extends React.Component {
  render() {
   return (
     <Card style={{marginLeft:"10px",marginRight:"10px"}}>
  <CardHeader
    title={this.props.title}
    subtitle={this.props.no}
    actAsExpander={true}
    showExpandableButton={true}
  />
  <CardText expandable={true}>
  <div style={styles.root}>
    <GridList style={styles.gridList} cols={1}>
      {this.props.tilesData.map((tile) => (
        <GridTile
          key={tile.img}
          title={tile.name}
          subtitle={tile.amount}
          actionIcon={<DropDownMenuLongMenuExample />}
          titleStyle={styles.titleStyle}
          titleBackground="linear-gradient(to top, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0.3) 70%,rgba(0,0,0,0) 100%)"
        >
          <img src={tile.img} />
        </GridTile>
      ))}
    </GridList>
  </div>
  </CardText>
</Card>
      );
   }
}

export default GridListExampleSingleLine;



